@layer base {
  * {
    border-color: var(--border);
    outline-color: var(--ring);
    @supports (color: color-mix(in lab, red, red)) {
      outline-color: color-mix(in oklab, var(--ring) 50%, transparent);
    }
  }
  body {
    background-color: var(--background);
    color: var(--foreground);
    font-family: "Satoshi",
      "Inter",
      -apple-system,
      BlinkMacSystemFont,
      sans-serif;
    font-feature-settings: "cv11", "ss01", "ss02";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    letter-spacing: -0.01em;
  }
}
@layer base {
  :where(:not(:has([class*=" text-"]), :not(:has([class^="text-"])))) {
    h1 {
      font-size: var(--text-2xl);
      font-weight: var(--font-weight-medium);
      line-height: 1.2;
      letter-spacing: -0.03em;
    }
    h2 {
      font-size: var(--text-xl);
      font-weight: var(--font-weight-medium);
      line-height: 1.3;
      letter-spacing: -0.02em;
    }
    h3 {
      font-size: var(--text-lg);
      font-weight: var(--font-weight-medium);
      line-height: 1.4;
      letter-spacing: -0.015em;
    }
    h4 {
      font-size: var(--text-base);
      font-weight: var(--font-weight-medium);
      line-height: 1.5;
    }
    p {
      font-size: var(--text-base);
      font-weight: var(--font-weight-normal);
      line-height: 1.6;
    }
    label {
      font-size: var(--text-base);
      font-weight: var(--font-weight-medium);
      line-height: 1.5;
    }
    button {
      font-size: var(--text-base);
      font-weight: var(--font-weight-medium);
      line-height: 1.5;
      letter-spacing: -0.01em;
    }
    input {
      font-size: var(--text-base);
      font-weight: var(--font-weight-normal);
      line-height: 1.5;
    }
  }
}
html {
  font-size: var(--font-size);
  scroll-behavior: smooth;
}
.gradient-bg {
  background: linear-gradient(135deg, #1a0b3d 0%, #7e1fff 50%, #ff1f70 100%);
  position: relative;
  overflow: hidden;
}
.gradient-bg::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    135deg,
    rgba(126, 31, 255, 0.95) 0%,
    rgba(255, 31, 112, 0.95) 100%
  );
  opacity: 0;
  transition: opacity 0.4s ease;
}
.gradient-bg:hover::before {
  opacity: 1;
}
.gradient-bg::after {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(
    circle at 50% 0%,
    rgba(255, 255, 255, 0.1) 0%,
    transparent 50%
  );
  opacity: 0.5;
  pointer-events: none;
}
.gradient-text {
  background: linear-gradient(
    135deg,
    #4d1d99 0%,
    #7e1fff 30%,
    #ff1f70 60%,
    #00ffd5 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 200% 200%;
  animation: gradient-shift 8s ease infinite;
}
@keyframes gradient-shift {
  0%,
  100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}
.glass-card {
  background: rgba(5, 5, 15, 0.85);
  backdrop-filter: blur(28px) saturate(180%);
  -webkit-backdrop-filter: blur(28px) saturate(180%);
  border: 1px solid rgba(126, 31, 255, 0.12);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.6),
    0 2px 8px rgba(126, 31, 255, 0.08),
    inset 0 1px 0 rgba(255, 255, 255, 0.08);
  position: relative;
}
.glass-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.2),
    transparent
  );
  opacity: 0.6;
}
.neon-glow {
  box-shadow: 0 8px 32px rgba(126, 31, 255, 0.18),
    0 2px 8px rgba(255, 31, 112, 0.12),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  transition: box-shadow 0.4s cubic-bezier(0.4, 0, 0.2, 1),
    transform 0.3s ease;
}
.neon-glow:hover {
  box-shadow: 0 16px 56px rgba(126, 31, 255, 0.28),
    0 4px 16px rgba(255, 31, 112, 0.22),
    0 0 80px rgba(0, 255, 213, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
}
.metallic-divider {
  height: 1px;
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(126, 31, 255, 0.3) 20%,
    rgba(255, 31, 112, 0.5) 50%,
    rgba(0, 255, 213, 0.3) 80%,
    transparent 100%
  );
  position: relative;
  overflow: hidden;
}
.metallic-divider::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(255, 255, 255, 0.3) 50%,
    transparent 100%
  );
  animation: shimmer 3s linear infinite;
}
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
.card-tilt {
  transform-style: preserve-3d;
  transition: transform 0.6s cubic-bezier(0.23, 1, 0.32, 1);
}
.card-tilt:hover {
  transform: perspective(1000px) rotateX(2deg) rotateY(2deg) translateZ(10px);
}
.parallax-slow {
  will-change: transform;
  transition: transform 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}
input:focus,
textarea:focus,
select:focus {
  outline: none;
  border-color: rgba(126, 31, 255, 0.5);
  box-shadow: 0 0 0 3px rgba(126, 31, 255, 0.1),
    0 8px 24px rgba(126, 31, 255, 0.15);
}
* {
  transition: color 0.3s ease,
    background-color 0.3s ease,
    border-color 0.3s ease;
}
.holo-button {
  position: relative;
  overflow: hidden;
  isolation: isolate;
}
.holo-button::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    45deg,
    transparent 30%,
    rgba(255, 255, 255, 0.1) 50%,
    transparent 70%
  );
  transform: translateX(-100%);
  transition: transform 0.6s ease;
}
.holo-button:hover::before {
  transform: translateX(100%);
}
.neon-glow {
  box-shadow: 0 0 20px rgba(139, 31, 255, 0.4),
    0 0 40px rgba(255, 31, 112, 0.2),
    inset 0 0 20px rgba(139, 31, 255, 0.1);
  transition: box-shadow 0.3s ease;
}
.neon-glow:hover {
  box-shadow: 0 0 30px rgba(139, 31, 255, 0.6),
    0 0 60px rgba(255, 31, 112, 0.4),
    inset 0 0 30px rgba(139, 31, 255, 0.2);
}
@keyframes ticker {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
.ticker-animation {
  animation: ticker 40s linear infinite;
}
@keyframes glow-pulse {
  0%,
  100% {
    filter: drop-shadow(0 0 20px rgba(139, 31, 255, 0.5));
  }
  50% {
    filter: drop-shadow(0 0 40px rgba(255, 31, 112, 0.6));
  }
}
.glow-pulse {
  animation: glow-pulse 4s ease-in-out infinite;
}
@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-15px);
  }
}
.float-animation {
  animation: float 6s ease-in-out infinite;
}
@keyframes particles {
  0% {
    transform: translateY(0) translateX(0);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100vh) translateX(50px);
    opacity: 0;
  }
}
.particles {
  position: absolute;
  inset: 0;
  overflow: hidden;
  pointer-events: none;
}
.particle {
  position: absolute;
  width: 2px;
  height: 2px;
  background: rgba(139, 31, 255, 0.6);
  border-radius: 50%;
  animation: particles 15s linear infinite;
}
@keyframes shimmer {
  0% {
    background-position: -200% center;
  }
  100% {
    background-position: 200% center;
  }
}
.shimmer {
  background: linear-gradient(
    90deg,
    transparent,
    rgba(139, 31, 255, 0.2),
    transparent
  );
  background-size: 200% 100%;
  animation: shimmer 3s ease-in-out infinite;
}
.logo-watermark {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.03;
  pointer-events: none;
  z-index: 0;
}
.card-reflection {
  position: relative;
}
.card-reflection::after {
  content: "";
  position: absolute;
  bottom: -50%;
  left: 0;
  right: 0;
  height: 50%;
  background: linear-gradient(to bottom, rgba(139, 31, 255, 0.05), transparent);
  transform: scaleY(-1);
  opacity: 0.3;
  pointer-events: none;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: rgba(15, 15, 15, 0.5);
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #8b1fff 0%, #ff1f70 100%);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #9d31ff 0%, #ff3080 100%);
}
::selection {
  background: rgba(139, 31, 255, 0.3);
  color: white;
}
*:focus-visible {
  outline: 2px solid rgba(139, 31, 255, 0.5);
  outline-offset: 2px;
}
input,
textarea,
select {
  background: rgba(15, 15, 15, 0.8);
  border: 1px solid rgba(139, 31, 255, 0.2);
  transition: all 0.3s ease;
}
input:focus,
textarea:focus,
select:focus {
  background: rgba(15, 15, 15, 0.95);
  border-color: rgba(139, 31, 255, 0.5);
  box-shadow: 0 0 20px rgba(139, 31, 255, 0.2);
}
.flip-card {
  perspective: 1000px;
}
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}
.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}
.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: inherit;
}
.flip-card-back {
  transform: rotateY(180deg);
}
@supports (padding: max(0px)) {
  .pb-safe {
    padding-bottom: max(8px, env(safe-area-inset-bottom));
  }
  .pt-safe {
    padding-top: max(8px, env(safe-area-inset-top));
  }
}
.scrollbar-hide::-webkit-scrollbar {
  display: none;
}
.scrollbar-hide {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.touch-target {
  min-width: 44px;
  min-height: 44px;
  -webkit-tap-highlight-color: transparent;
}
.momentum-scroll {
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
}
@keyframes swipe-right {
  from {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }
  to {
    transform: translateX(150%) rotate(20deg);
    opacity: 0;
  }
}
@keyframes swipe-left {
  from {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }
  to {
    transform: translateX(-150%) rotate(-20deg);
    opacity: 0;
  }
}
.swipe-right {
  animation: swipe-right 0.4s ease-out forwards;
}
.swipe-left {
  animation: swipe-left 0.4s ease-out forwards;
}
@keyframes pull-bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
.pull-refresh {
  animation: pull-bounce 1s ease-in-out infinite;
}
.bottom-sheet {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(
    to bottom,
    rgba(5, 5, 15, 0.98),
    rgba(0, 0, 0, 0.98)
  );
  backdrop-filter: blur(20px);
  border-radius: 24px 24px 0 0;
  z-index: 100;
  transform: translateY(100%);
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.bottom-sheet.open {
  transform: translateY(0);
}
.bottom-sheet-handle {
  width: 40px;
  height: 4px;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 2px;
  margin: 12px auto;
}
.card-stack {
  position: relative;
  width: 100%;
  height: 500px;
}
.card-stack-item {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  touch-action: pan-y;
}
@media (max-width: 768px) {
  .glass-card {
    backdrop-filter: blur(20px) saturate(150%);
    -webkit-backdrop-filter: blur(20px) saturate(150%);
  }
}
.snap-x {
  scroll-snap-type: x mandatory;
}
.snap-center {
  scroll-snap-align: center;
}
.snap-start {
  scroll-snap-align: start;
}
@media (max-width: 768px) {
  :root {
    --font-size: 16px;
  }
  p {
    line-height: 1.7;
  }
  button {
    min-height: 44px;
  }
}
@keyframes haptic-pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(1);
  }
}
.haptic-feedback:active {
  animation: haptic-pulse 0.15s ease-out;
}
.swipe-hint {
  position: relative;
}
.swipe-hint::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 60px;
  background: linear-gradient(to left, rgba(0, 0, 0, 0.8), transparent);
  pointer-events: none;
}
@media (max-width: 768px) {
  .gradient-bg {
    will-change: auto;
  }
  .gradient-text {
    background-size: 100% 100%;
    animation: none;
  }
  .particle {
    animation-duration: 20s;
  }
}
.mobile-progress {
  height: 3px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 2px;
  overflow: hidden;
}
.mobile-progress-bar {
  height: 100%;
  background: linear-gradient(90deg, #7e1fff, #ff1f70);
  transition: width 0.3s ease;
}
@media (max-width: 768px) {
  [data-sonner-toaster] {
    bottom: 80px !important;
  }
}
.thumb-zone {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 66.666vh;
  pointer-events: none;
  z-index: 9999;
}
.mobile-menu-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(4px);
  z-index: 40;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.mobile-menu-backdrop.open {
  opacity: 1;
}
@media (max-width: 768px) {
  *:focus-visible {
    outline: 3px solid rgba(126, 31, 255, 0.5);
    outline-offset: 3px;
  }
}
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}
@media (max-width: 768px) {
  body {
    background: #000000;
  }
}
